import React from 'react'
import clsx from 'clsx'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	defaultListIcon: props => ({
		'textAlign': 'center',
		...theme.components.icon,
		...props.styles
	}),
}));


export default React.memo((props) => {
	const { icon, customClassNames } = props;
	const classes = useStyles(props.styles);
	
	let classNames = Array.isArray(customClassNames) ? customClassNames : [customClassNames];
	classNames = classNames.map(className => (classes[className] ? classes[className] : className));
	
	// base64 image as icon
	if (icon.indexOf('data:image') > -1 && icon.indexOf('base64') > -1) {
		return <img widh="32" height="32" className={clsx(classNames)} src={icon} alt="" />
	}
	
	// icon
	return <span className={clsx(classNames, 'mdi mdi-' + icon)}></span>
});
