import React, { useState } from 'react'

import Connection from '../../controller/Connection'
import { joinConfig } from '../../helpers/utils'


export default function DisplayImage(props) {
	let config = joinConfig(props.config, require('./DisplayImage.config.js').default);
	
	//
	const [img,setImg] = useState(getImage(config.url));
	
	if (!config.url) {
		return null;
	}
	
	// detect state and retrieve its value
	const regex = RegExp('.*\\.\\d+\\..*');
	if (config.url.indexOf('data:image') === -1 && config.url.indexOf('http://') === -1 && config.url.indexOf('https://') === -1 && regex.test(config.url)) {
		const socket = Connection.getConnection;
		
		// state change
		const stateChange = (id, state) => id === config.url && state.val && setImg(getImage(state.val));
		
		// subscribe state
		socket.subscribeState(config.url, stateChange);
		
		// request state
		socket.getState(config.url, (err, state) => stateChange(config.url, state));
	}
	
	return <img width={config.width || '100%'} height={config.height || '100%'} src={img} title={props.title} alt="" />
}

function getImage(img) {
	return img.indexOf('data:image') === -1 && img.indexOf('http://') === -1 && img.indexOf('https://') === -1 ? 'http://' + img : img;
}