import React, { useState } from 'react'
import clsx from 'clsx'

import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

//import i18n from '../../controller/i18n'
import Settings from '../../controller/Settings'



/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	QuickJump: {
		position: 'fixed',
		bottom: 0,
		//left: 'calc(50% - 100px)',
		//width: 240,
		width: '100%',
		maxHeight: 500,
		backgroundColor: '#fff',
		textAlign: 'center',
		padding: '5px 0 10px 0',
		borderTop: '2px solid #eee',
		zIndex: 1000,
		overflow: 'auto'
	},
	QuickJumpOpener: {
		cursor: 'pointer', 
	},
	JumpItemList: {
		width: '100%'
	},
	JumpItem: {
	},
	JumpItemHighlighted: {
		backgroundColor: theme.palette.primary.main,
		color: '#fff'
	},
	JumpItemIconHighlighted: {
		color: '#fff'
	}
}));


export default function QuickJump(props) {
	const classes = useStyles();
	
	const [open,setOpen] = useState(false);
	const onJump = (drawer, tab, widget) => {
		setOpen(false);
		props.onJump(drawer, tab, widget);
	}
	
	const openQuickJump = () => {
		setOpen(prevOpen => prevOpen !== true);
	}
	
	const layout = Settings.getLayout();
	let QuickJumpComponents = [];
	
	// drawer
	for (let drawerIndex in layout) {
		let drawer = layout[drawerIndex];
		
		// tabs
		for (let tabIndex in drawer.tabs) {
			let tab = drawer.tabs[tabIndex];
			QuickJumpComponents.push(<JumpItem key={'JumpList-' + drawerIndex + '-' + tabIndex} highlighted={true} button={false} icon={tab.icon} title={tab.title} />);
			
			// fullscreen
			if (tab._fullscreen) {
				QuickJumpComponents.push(<JumpItem key={'JumpList-' + drawerIndex + '-' + tabIndex + '-fullscreen'} onJump={() => onJump({ ...drawer, index: drawerIndex }, { ...tab, index: tabIndex })} icon={tab.icon || 'home'} title={tab.title} />);
			}
			
			// columns
			else {
				for (let columnIndex in tab.columns) {
					let column = tab.columns[columnIndex];
					
					// widgets
					for (let widgetIndex in column) {
						let widget = column[widgetIndex];
						QuickJumpComponents.push(<JumpItem key={'JumpList-' + drawerIndex + '-' + tabIndex + '-' + column + '-' + widgetIndex} onJump={() => onJump({ ...drawer, index: drawerIndex }, { ...tab, index: tabIndex }, { ...widget, index: widgetIndex, columnIndex: columnIndex })} icon={widget.icon || 'home'} title={widget.title} />);
					}
				}
			}
		}
	}
	
	return (
		<div className={classes.QuickJump}>
			<div className={classes.QuickJumpOpener} onClick={openQuickJump}>
				<span className={'mdi mdi-chevron-' + (open ? 'down' : 'up')}></span>
			</div>
			
			{open && <List classes={{ root: classes.JumpList }}>
				{QuickJumpComponents}
			</List>}
		</div>
	);
}

function JumpItem(props) {
	const classes = useStyles();
	
	return (
		<ListItem
			classes={{ root: clsx(classes.JumpItem, props.highlighted && classes.JumpItemHighlighted) }}
			button={props.button !== undefined ? props.button : true}
			onClick={props.onJump}
			>
			
			<ListItemIcon classes={{ root: clsx(classes.JumpItemIcon, props.highlighted && classes.JumpItemIconHighlighted) }}>
				<span className={'mdi mdi-' + props.icon.replace('mdi-', '')}></span>
			</ListItemIcon>
			<ListItemText primary={props.highlighted ? <strong>{props.title}</strong> : props.title} />
		</ListItem>
	);
}