import React, { useState, useEffect } from 'react'

import Devices from '../../controller/Devices'
import Log from '../../controller/Log'

//import { joinConfig } from '../../helpers/utils'


export default function CustomHTML(props) {
	const { devices } = props;
	//let config = joinConfig(props.config, require('./CustomHTML.config.js').default);
	
	return Devices.forEachDevice(devices, (device, index, options) => <CustomHTMLElement key={'CustomHTMLElement-' + index + '-Device-' + device.id} device={device} options={options} />);
}

function CustomHTMLElement(props) {
	const { device, options } = props;
	
	const [html,setHTML] = useState('');
	const stateChange = (stateKey, state, device) => {
		setHTML(state.val);
	}
	
	useEffect(() => {
		
		// receive state update
		device.listen('stateChange', stateChange);
		
		// request device state
		device.requestDeviceState(options.primaryState, true).catch(res => {
			Log._('Modules (CustomHTML)', res.error.message, 'error', true, res.error.stack);
		});
		
	}, [device, options]);
	
	return <div dangerouslySetInnerHTML={{ __html: html }} />
}
